import React from "react";

const Form = props => (
	<form onSubmit={props.getWeather}>
		<input type="text" name="latitude" placeholder="Latitude"/>
		<input type="text" name="longitude" placeholder="Longitude"/>
        <input type="date" id="date" name="date"></input>
		<button>Get Forecast</button> 
         {	
             props.today && <span className="today"> Today is: 
                 <span className="todayDate"> { props.today }</span>                
             </span>            
         }
        <br></br>
        <span class="note"><strong>Note:</strong><i> "date" is not supported in Safari or Internet Explorer 11 (or earlier).</i></span>
	<br></br>
    <span class="valid"><strong>Example of values:</strong><i> Latitude: 37.8267 and Longitude: -122.4233 </i></span>
    </form>
);

export default Form;